.features__title-wrapper {
  display: flex;
  flex-direction: column;
  padding: 46px 20px 10px 20px;

  text-align: center;

  background-color: $color-white;

  @media (min-width: $desktop-width) {
    padding: 77px 130px 67px 130px;
  }
}

.features__title {
  margin: 0;
  margin-bottom: 19px;
  padding: 0;

  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
}

.features__list {
  margin: 0;
  padding: 100px 20px 20px 20px;

  list-style-type: none;

  background-color: $color-white-smoke;

  @media (min-width: $tablet-width) {
    padding: 20px 130px 20px 330px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 68px;
    padding: 77px 130px 67px 130px;
  }
}

.features__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__item-title {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;

  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}

.features__item-text {
  margin-bottom: 43px;

  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
}
